export default {
	data() {
		return {
			currentTab: 0,
			showTab: true,
			windowHeight: 0,
			windowWidth: 0,
			tabPosition: 0
		}
	},
	onLoad: function() {
		uni.getSystemInfo({
			success: (res) => {
				this.windowHeight = res.windowHeight;
				this.windowWidth = res.windowWidth;
				console.log(this.windowHeight);
				console.log(this.windowWidth);
			}
		})
	},
	//分享按钮事件处理;
	onShareAppMessage: function(res) {
		uni.showShareMenu({
			withShareTicket:true,
			success:function(res){
				console.log('成功');
				console.log(res);
			},
			fail:function(res){
				console.log('你似不似龙鸣');
				console.log(res);
			}
		})
		return {
			title:'你浓郁的腿毛,是我一生的信仰',
			path:'/pages/index/index',
			imageUrl:'/static/foothair.jpg'
		}
		
	},
	methods: {
		//滑动切换选项卡;
		swiperTab: function(e) {
			this.currentTab = e.detail.current;
			let singleNavWidth = this.windowWidth / 7;
			this.tabPosition = (this.currentTab - 2) * singleNavWidth;
		},
		//点击切换选项卡;
		clickTab: function(event) {
			this.currentTab = event.currentTarget.dataset.index;
		},
		scroll: function(e) {
			console.log(e);
			let y = Math.round(e.detail.deltaY);
			if (y > 0) {
				if (y >= 10) {
					this.showTab = true;
				}
			} else if (y < 0) {
				if (y <= -10) {
					this.showTab = false;
				}
			}
			console.log(y);
		},
		scrollToupper: function(e) {
			console.log('滚动到顶部');
			console.log(e);
		},
		deleteItem: function() {
			uni.showModal({
				title: '操作提示',
				content: '删除后不可恢复,是否删除?',
				success: function(res) {
					if (res.confirm) {
						uni.showLoading({
							title: '加载中,请稍后...'
						})
						//调用删除接口,若返回有效数据,则隐藏加载状态,刷新页面;

						//刷新页面;

						//隐藏加载状态;
						uni.hideLoading();
					}
				}
			})
		},
		shareItem: function() {
			uni.showShareMenu({
				withShareTicket:true,
				success:function(res){
					console.log('成功');
					console.log(res);
				},
				fail:function(res){
					console.log('你似不似龙鸣');
					console.log(res);
				}
			})
		}
	}
}
